import React from 'react';
import { account } from '../Account/Account.js';
import { Menu, Layout, Breadcrumb } from 'antd';
import {
  DashboardOutlined,
  BankOutlined,
  ContactsOutlined,
  CalendarOutlined,
  AccountBookOutlined
} from '@ant-design/icons';
//import './Inner.less';

const { Content, Footer, Sider } = Layout;

class Dashboard extends React.Component{
  constructor(){
    super()
    this.state = {
      // sidebar options
      options: [
        {key: 0, name: "Dashboard", icon: <DashboardOutlined />},
        {key: 1, name: "Companys", icon: <BankOutlined />},
        {key: 2, name: "Contacts", icon: <ContactsOutlined />},
        {key: 3, name: "Calendar", icon: <CalendarOutlined />},
        {key: 4, name: "Sale", icon: <AccountBookOutlined />},
      ],
      // state of sidebar collapsed
      collapsed: false,
      currentTab: 0,
    }
    import('./Inner.less');
  }

  // handle change tap event
  changeTab(e){
    this.setState({currentTab: e.key});
  }

  // display sidebar
  sideBars(){
    let output = [];
    this.state.options.forEach((item, i) => {
      output.push((<Menu.Item key={item.key} icon={item.icon} >{item.name}</Menu.Item>))
    });
    return output;
  }

  onCollapse(){
    this.setState({collapsed: !this.state.collapsed});
  }

  render(){
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse.bind(this)} width={250} style={{ minHeight: '100vh' }}>
          <div style={{height: 250}} />
          <Menu
            selectedKeys={[this.state.currentTab]}
            mode="inline"
            theme="dark"
            onClick={this.changeTab.bind(this)}
            inlineCollapsed={this.state.collapsed}
          >
            {this.sideBars()}
          </Menu>
        </Sider>
        <Layout className="site-layout">
          <Content>
            {/* todo */}
            <div className="header">{this.state.options[this.state.currentTab].name}</div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>This will be a footer</Footer>
        </Layout>
      </Layout>
    )
  }
}

export default Dashboard;
